import { renderUnorderedListButton } from './components/unorderedListButton';
import OrderedListButton from './components/orderedListButton';
import TaskListButton from './components/taskListButton';

export default function toolbar(listConfig) {
  const listLocaleConfig = listConfig.locale || {};
  return {
    buttons: {
      ulist: renderUnorderedListButton(listConfig),
      olist: OrderedListButton,
      tlist: TaskListButton,
    },
    locale: listLocaleConfig,
    getTarget(controller) {
      if (
        controller.value.data.listSelections &&
        controller.value.data.listSelections.length > 0
      ) {
        return 'listSymbol';
      }
      return null;
    },
    stickerList: true,
  };
}
